<template>
	<div>
		<el-dialog :modal="modal" :visible="dialogVisible" :center="false" custom-class="land-detail" :show-close="false" ref="plotlist" :close-on-click-modal="false">
			<!--弹出层关闭按钮-->
			<div class="popclose" @click="dialogVisible = false"></div>
			<div class="pop">
				<div class="land-detail-header">
					<span>地块明细</span>
				</div>
				<div class="pop-main">
					<div class="pop-content">
						<!-- <div class="land-detail-notice">
							<i class="el-icon-s-opportunity"></i>
							<span>该地块的经营主体未在神农口袋填报生产档案</span>
						</div> -->

						<div class="land-detail-desc">
							<div v-if="landDetailData.ZHUTINAME || landDetailData.COMPANY" class="land-detail-name">{{ landDetailData.ZHUTINAME || landDetailData.COMPANY }}</div>
							<div class="land-detail-info" v-if="landDetailData.companyType || landDetailData['主体类型']">
								<b>主体类型：</b>
								<span>{{ landDetailData.companyType ? landDetailData.companyType : landDetailData['主体类型'] }}</span>
							</div>
							<div class="land-detail-info">
								<b>行政区：</b>
								<span>{{ landDetailData.DISTRICT }}</span>
							</div>
							<div v-if="landDetailData['镇'] || landDetailData.TOWN" class="land-detail-info">
								<b>街镇：</b>
								<span>{{ landDetailData['镇'] ? landDetailData['镇'] : landDetailData.TOWN ? landDetailData.TOWN : '' }}</span>
							</div>
							<div class="land-detail-info margin-top">
								<b>地块ID：</b>
								<span>{{ landDetailData.DPDKID || landDetailData.DKID }}</span>
							</div>
							<div class="land-detail-info" v-if="landDetailData.LANDTYPE">
								<b>地块类型：</b>
								<span>{{ landDetailData.LANDTYPE }}</span>
							</div>
							<!-- <div class="land-detail-info" v-if="landDetailData.Shape_Leng">
								<b>地块周长：</b>
								<span>{{ parseFloat(Number(landDetailData.Shape_Leng).toFixed(4)) }}米</span>
							</div> -->
							<div class="land-detail-info" v-if="landDetailData.AREAMU">
								<b>地块面积：</b>
								<span>{{ parseFloat(Number(landDetailData.AREAMU).toFixed(4)) }}亩</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</el-dialog>
	</div>
</template>
<!-- 神农口袋id （种植系统id 测绘院id 需要调用接口兑换神农口袋id） -->
<script>
export default {
	name: 'land-detail-pop',
	props: {
		visible: {
			default: false,
			type: Boolean
		},
		// 神农口袋地块id
		landDetailData: {
			type: Object,
			default: () => {
				return {};
			}
		},
		modal: {
			// 是否显示蒙层
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			// landDetailData: {}
		};
	},
	created() {
		window.layer.closeAll();
		// console.log('this.landDetailData=======>', this.landDetailData)
	},
	computed: {
		dialogVisible: {
			get() {
				return this.visible;
			},
			set(val) {
				this.$emit('changePop', val);
			}
		}
	}
};
</script>
<style scoped lang="scss">
.pop {
	display: flex;
	flex-direction: column;
	height: 100%;
	.pop-main {
		flex: 1;
		height: 100%;
		padding-top: 18px;

		.pop-content {
			width: 620px;
			height: 450px;
			margin: 0 auto;
			padding: 20px 30px 0;
			background: #061f36;

			.land-detail-notice {
				display: flex;
				align-items: center;
				height: 30px;
				padding-left: 20px;
				background-color: #0d141d;
				border-radius: 15px;
				color: #ef7727;
				font-size: 14px;

				span {
					margin-left: 5px;
				}
			}

			.land-detail-desc {
				margin-top: 20px;
				padding-top: 40px;
				padding-left: 20px;
				// border-top: 1px solid #177199;
				text-align: left;

				.land-detail-name {
					margin-bottom: 50px;
					font-size: 24px;
					font-weight: bold;
					color: #9ae5ee;
				}
				.land-detail-info {
					margin-bottom: 20px;
					font-size: 14px;
					color: #9ae5ee;

					b {
						font-weight: bold;
					}

					span {
						color: #6dc1cb;
					}
				}
			}
		}
	}
	.margin-top {
		margin-top: 50px;
	}
}
.land-detail-header {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 76px;
	width: 100%;

	span {
		color: #00f6ff;
		font-size: 18px;
		font-weight: bold;
	}
}
</style>
<style scoped lang="less">
.ftSize(@size:12px) {
	font-size: @size;
}

.comColor() {
	border: #eee;
	bg: #fff;
	txt: #fff;
}

.darkBg(@opacity:0.5) {
	background: rgba(0, 0, 0, @opacity);
}

.txtAlign(@pos:center) {
	text-align: @pos;
}

.radius(@r:5px) {
	border-radius: @r;
}

.position(@type:absolute,@top:0,@right:0,@bottom:auto,@left:auto) {
	position: @type;
	top: @top;
	right: @right;
	bottom: @bottom;
	left: @left;
}

.flex-center(@x:center,@y:center) {
	display: flex;
	display: -webkit-flex;
	flex-flow: row nowrap;
	justify-content: @x;
	align-items: @y;
}

.popclose {
	.position(absolute, 22px, 20px);
	width: 50px;
	height: 50px;
	z-index: 9999;
	background: url('../../assets/images/pop_close.png') no-repeat center 100%/100%;
	.position(absolute, 22px, -48px);
	cursor: pointer;
}

/deep/ .el-switch__label,
/deep/ .el-switch__label.is-active {
	color: #b3bfe0;
	font-size: 12px;
}

/deep/ .el-switch__core:after {
	box-shadow: 0 0 10px #888;
}

/deep/ .el-dialog__wrapper {
	.position(absolute, 2vh, calc((100% - 700px) / 2));
	overflow: initial;
}

/deep/ .el-dialog {
	margin: 0;
}

/deep/ .land-detail {
	margin: 0 !important;
	width: 820px;
	height: 650px;
	padding: 26px 30px 30px;
	box-sizing: border-box;
	border: none;
  background: url('../../assets/images/plotpop_bg.png') no-repeat center 100%/100%;
  background-size: 100% 100%;
	box-shadow: none;
	-webkit-box-shadow: none;

	.el-dialog__header {
		display: none;
	}

	.el-dialog__body {
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		padding: 0;
		text-align: center;
	}
}
</style>



